import { noticeDetail } from '@/api'
import uParse from '@/components/uParse/src/wxParse.vue'
export default {
	name: '',
	data () {
		return {
			id: '',
			detail: ''
		}
	},
	onLoad (options) {
		this.id = options.id;
		this._noticeDetail()
	},
	methods: {
		async _noticeDetail () {
			let result = await noticeDetail ({
				gg_id: this.id
			})
			this.detail = result.data
		}
	},
	components: {
		uParse
	}
}
